import { withSearchGames } from "../Pages/LoL/SearchGamesResultPage/withSearchGames";
import SearchGamesResultPage from "../Pages/LoL/SearchGamesResultPage/SearchGamesResultPage";
import SearchGamesLandingPage from "../Pages/LoL/SearchGamesLandingPage/SearchGamesLandingPage";

export enum PageId {
  Hello = "Hello",
  SearchLolGamesResult = "SearchLolGamesResult"
}

export interface IPageRoute {
  component: React.ComponentClass,
  id: PageId,
  path: string,
  title: string
}

export class PageRoutes {
  public static SearchLolGamesResult: IPageRoute = {
    component: withSearchGames(SearchGamesResultPage),
    id: PageId.SearchLolGamesResult,
    path: "/lol/games/search/results/streamer/:twitchLogin/summoner/:summonerName/champion/:championName",
    title: "LoL Games"
  };

  public static SearchLolGamesLanding: IPageRoute = {
    component: SearchGamesLandingPage,
    id: PageId.SearchLolGamesResult,
    path: "/lol/games/search",
    title: "Search For LoL Games"
  }
}
