import React from 'react';

import { Row, Col, Icon } from 'antd';

import './SearchGamesLandingPage.css';
import SearchGamesForm from "../SearchGamesForm/SearchGamesForm";
import { getSearchGameVideosDefaultParameters } from "../../../../state/lol/games/gamesApi";


interface ISearchGamesLandingPageProps {
}

interface ISearchGamesLandingPageState {
  twitchLogin?: string,
  summonerName?: string,
  championName?: string
}

export class SearchGamesLandingPage extends React.Component<ISearchGamesLandingPageProps> {

  state: ISearchGamesLandingPageState = {
  };

  public render() {
    const isLoading = this.state.twitchLogin === undefined;

    return (
      <div>
        <Row>
          <Col span={12} offset={6} style={{textAlign: "center"}}>
            {
              isLoading ?
                <Icon type="loading" /> :
                <SearchGamesForm twitchLogin={this.state.twitchLogin} summonerName={this.state.summonerName} championName={this.state.championName} />
            }
          </Col>
        </Row>
      </div>
    );
  }

  public componentDidMount() {
    getSearchGameVideosDefaultParameters().then(request => {
      const { twitchLogin, summonerName, championName } = request;
      console.log(`twitchLogin=${twitchLogin}`);
      this.setState({
        twitchLogin,
        summonerName,
        championName
      })
    })
  }
}

export default SearchGamesLandingPage;
