import React from "react";

import { Input, Form } from "antd";
import { FormComponentProps } from "antd/es/form";
import LinkButton from "../../../Common/LinkButton";

interface ISearchGamesFormProps extends FormComponentProps {
  twitchLogin?: string,
  summonerName?: string,
  championName?: string
}

interface FormState {
  twitchLogin?: string,
  summonerName?: string,
  championName?: string
}

class SearchGamesFormInner extends React.Component<ISearchGamesFormProps> {
  state: FormState = {
    twitchLogin: this.props.twitchLogin,
    summonerName: this.props.summonerName,
    championName: this.props.championName
  };

  public render() {
    const { getFieldDecorator } = this.props.form;

    const { twitchLogin, summonerName, championName } = this.state;

    const itemLayout = { labelCol: { span: 8 }, wrapperCol: { span: 16 }};
    return (
      <Form onChange={this.formSubmitChange} onSubmit={this.formSubmitChange}>
          <Form.Item {...itemLayout} label={"Twitch.tv"}>
            {getFieldDecorator("twitchLogin", {initialValue: twitchLogin })(
              <Input size={"large"} placeholder={"twitch login name"} />
            )}
          </Form.Item>

        <Form.Item {...itemLayout} label={"Summoner Name"}>
          {getFieldDecorator("summonerName", {initialValue: summonerName})(
            <Input size={"large"} placeholder={"LoL summoner name"} />
          )}
        </Form.Item>

        <Form.Item {...itemLayout} label={"Champion Name"}>
          {getFieldDecorator("championName", {initialValue: championName})(
            <Input size={"large"} placeholder={"LoL champion name"} />
          )}
        </Form.Item>

        <Form.Item>
          {getFieldDecorator("search")(
            <LinkButton
              to={`/lol/games/search/results/streamer/${twitchLogin}/summoner/${summonerName}/champion/${championName}`}
              type={"primary"}
            >
              Search
            </LinkButton>
          )}
        </Form.Item>
      </Form>
    );

  }

  formSubmitChange = (e: React.ChangeEvent<HTMLFormElement>) => {
    e.preventDefault();

    const twitchLogin = this.props.form.getFieldValue("twitchLogin");
    const summonerName = this.props.form.getFieldValue("summonerName");
    const championName = this.props.form.getFieldValue("championName");

    this.setState({
      twitchLogin,
      summonerName,
      championName
    });
  }
}

export const SearchGamesForm = Form.create({name: "Search_Games"})(SearchGamesFormInner);
export default SearchGamesForm;
